.b-top-menu
{
    height: 132px;
    font:400 14px/16px 'Open Sans', sans-serif;
    //background-color: #3e3d41;

    &__fixed-top
    {
        left: auto;
        right: auto;
    }

    &__nav-brand
    {
        margin-right: 250px;
    }

    &__nav-item
    {
        margin-left: 42px;
    }

    &__nav-link
    {
        color: #f4f5f9;
        transition: all 0.3s ease;

        &:hover
        {
            color: #e74c3c;
            position: relative;
            transition: all 0.3s ease;

            &:after
            {
                background-color: #e74c3c;
                position: absolute;
                content: '';
                display: block;
                height: 1px;
                margin: 0 auto;
                margin-top: 15px;
                width: 80%;
                transition: all 0.5s ease;
            } 
        }
    }
    
}